export default {
  name: 'addUserForm',
  data () {
    return {
      item: {
        name: '',
        telephone: '',
        level: Number
      },
      level: [
        {
          value: this.PI,
          label: 'PI'
        },
        {
          value: this.assistant,
          label: 'assistant'
        },
        {
          value: this.PR,
          label: 'PR'
        },
        {
          value: this.student,
          label: 'student'
        },
        {
          value: this.stranger,
          label: 'stranger'
        }
      ]
    }
  },
  methods: {
    submit () {
      if (this.item.name === '' || this.item.telephone === '') {
        this.$notify.warning({
          title: '请输入姓名和电话!'
        })
        return
      }
      if (this.item.level === 0) {
        this.$notify.warning({
          title: '请选择用户身份!'
        })
        return
      }
      this.$axios({
        method: 'post',
        url: this.url + '/user/',
        data: JSON.stringify(this.item)
      }).then((user) => {
        if (user.status === 200) {
          alert('添加成功')
          this.item.level = 0
          this.item.name = ''
          this.item.telephone = ''
        } else {
          alert(user)
        }
      }).catch((err) => {
        alert(err)
      })
    }
  },
  created () {
    this.item.level = 0
  }
}



